	module.exports = "<p>Install from GitHub via NPM</p> <pre><v-code lang=bash>npm install sagalbot/vue-select</v-code></pre> <p>To use the vue-select component in your templates, simply import it, and register it with your component.</p> <pre><v-code lang=markup>&#x3C;template&#x3E;\n  &#x3C;div id=&#x22;myApp&#x22;&#x3E;\n    &#x3C;v-select :value.sync=&#x22;selected&#x22; :options=&#x22;options&#x22;&#x3E;&#x3C;/v-select&#x3E;\n  &#x3C;/div&#x3E;\n&#x3C;/template&#x3E;\n&#x3C;script&#x3E;</v-code>\n<v-code lang=javascript>import vSelect from \"vue-select\"\n  export default {\n    components: {vSelect},\n\n    data() {\n      return {\n        selected: null,\n        options: ['foo','bar','baz']\n      }\n    }\n  }\n&#x3C;/script&#x3E;</v-code>\n</pre>";

